import type { FC } from "react";
import type { Work } from "../../../types/resume";
import { toIcon } from "../../../utils";
import { FeatureArticle, Section, SubSection } from "../../molecules";

interface IExperienceSectionProps {
  works: Work[];
}

interface IExperienceSubtitleProps {
  name: string;
  startDate: string;
  endDate: string;
}

const DATE_RENDERER = new Intl.DateTimeFormat("en-GB", {
  year: "numeric",
  month: "long",
});

const ExperienceSubtitle: FC<IExperienceSubtitleProps> = ({
  name,
  startDate,
  endDate,
}) => {
  const start = new Date(startDate);
  const end = new Date(endDate);
  return (
    <>
      {name} <span className="font-extrabold">|</span>{" "}
      <span className="italic font-extralight">
        <time dateTime={startDate}>{DATE_RENDERER.format(start)}</time> -{" "}
        <time dateTime={endDate}>{DATE_RENDERER.format(end)}</time>
      </span>
    </>
  );
};

const ExperienceSection: FC<IExperienceSectionProps> = ({ works }) => {
  return (
    <Section title="Experience">
      {works.map((workExperience) => {
        return (
          <SubSection
            key={`${workExperience.name}-${workExperience.startDate}-${workExperience.endDate}`}
            title={workExperience.position}
            subtitle={
              <ExperienceSubtitle
                name={workExperience.name}
                startDate={workExperience.startDate}
                endDate={workExperience.endDate}
              />
            }
          >
            {workExperience.duties?.map((duty) => (
              <FeatureArticle
                key={duty.description}
                icon={toIcon(duty.icon)}
                className="md:w-1/3"
              >
                {duty.description}
              </FeatureArticle>
            ))}
          </SubSection>
        );
      })}
    </Section>
  );
};

export default ExperienceSection;
