import type { HeadFC, PageProps } from "gatsby";
import { StaticImage } from "gatsby-plugin-image";
import type { FC } from "react";
import { MainLayout } from "../components/layouts/mainLayout";
import { HeroBlock } from "../components/organisms";
import { ExperienceSection, SkillSection } from "../components/templates";
import resume from "../data/resume.json";
import type { ResumeSchema } from "../types/resume";

const TypedResume = resume as ResumeSchema;

const IndexPage: FC<PageProps> = () => {
  return (
    <MainLayout>
      <HeroBlock
        basic={TypedResume.basics}
        profileImage={
          <StaticImage
            src={"../images/marcus-gilmore-lim.jpg"}
            alt="A picture of Marcus Gilmore"
            imgClassName="w-full h-auto object-cover rounded-full"
          />
        }
      />
      <SkillSection skills={TypedResume.skills} />
      <ExperienceSection works={TypedResume.work} />
    </MainLayout>
  );
};

export default IndexPage;

export const Head: HeadFC = () => (
  <>
    <html lang="en" />
    <title>Home Page</title>
    <meta name="description" content="The portfolio of Marcus Gilmore-Lim, a Senior Software Engineer with 10 years of experience in building robust, scalable systems. A programming polyglot specializing in Java, Python, C#, cloud architecture, and DevOps." />
  </>
);
