import { type FC, Fragment } from "react";
import { twMerge } from "tailwind-merge";
import { H1, H2, LineBreak, Text } from "../../atoms";

interface IHeroTextBlockProps {
  className?: string;
  title: string;
  subtitle: string;
  heroText: string | string[];
}

const TextBlock: FC<IHeroTextBlockProps> = ({
  className,
  title,
  subtitle,
  heroText,
}) => {
  const text = Array.isArray(heroText) ? (
    heroText.map((text, index) => (
      <Fragment key={`heroText-${text}`}>
        <Text>{text}</Text>
        {index < heroText.length - 1 && <LineBreak />}
      </Fragment>
    ))
  ) : (
    <Text>{heroText}</Text>
  );

  return (
    <div className={twMerge("flex flex-col items-start", className)}>
      <H1>{title}</H1>
      <H2 className="mt-4 pb-6">{subtitle}</H2>
      {text}
    </div>
  );
};

export default TextBlock;
